@import '~theme.scss';

.root {
  @extend .container;
  .content {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, 204px);
    @media (max-width: $breakpoint-middle) {
      grid-template-columns: repeat(2, 204px);
    }
    @media (max-width: $breakpoint-mobile) {
      grid-template-columns: repeat(1, 204px);
    }
    column-gap: 56px;
    @include spacing(row-gap, 56);
    img {
      height: 100%;
      width: 100%;
    }
  }
  @include spacing(margin-bottom,160);
}

.root {
  @extend .container;
  @include spacing(margin-bottom, 160);
}

.tag {
  @include font-sizing(16);
  font-weight: bold;
  letter-spacing: 2.2px;
  color: #7f7592;
  @include spacing(margin-bottom, 16);
  text-align: center;
}

.title {
  @include font-sizing(48);
  font-weight: bold;
  color: #ffffff;
  @include spacing(margin-bottom, 56);
  letter-spacing: 2px;
  text-align: center;
}

@media (max-width: $breakpoint-middle) {
  .cover {
    display: none;
  }
}

.profileCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  .role {
    white-space: break-spaces;
    font-family: Roboto;
    @include font-sizing(20);
    color: #7f7592;
    letter-spacing: 0.4px;
  }
  .name {
    font-family: Roboto;
    @include font-sizing(20);
    font-weight: bold;
    letter-spacing: 0.4px;
    color: #ffffff;
    margin-bottom: 8px;
    margin-top: 16px;
  }
}

.photoGroup {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  width: 204px;
  height: 204px;

  @media (max-width: $breakpoint-middle) {
    width: 204px;
    height: 204px;
  }

  @media (max-width: $breakpoint-mobile) {
    width: 204px;
    height: 204px;
  }

  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    background-color: rgba(0,0,0,0.5);
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    a {
      color: gray;
      &:hover {
        color: white;
      }
      font-size: 48px;
      height: 48px;
    }
  }
  &:hover .overlay{
    visibility: visible;
  }
}