@import "~theme.scss";

.root {
  @extend .container;
  display: flex;
  @include spacing(padding-top, 24);
  @include spacing(padding-bottom, 24);
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  img {
    height: 20px;
    @media (max-width: $breakpoint-middle) {
      height: 16px;
    }
  }
  z-index: 100;
  div:last-child a {
    color: #ffffff59;
    text-shadow: 0px 4px 32px #000000e1;
    @include font-sizing(20);
    font-weight: bold;
    &:hover {
      color: white;
    }
    &.selected {
      color: white;
      @media (max-width: $breakpoint-mobile) {
        display: none;
      }
    }
    &:last-child {
      @media (max-width: $breakpoint-mobile) {
        display: none;
      }
    }
    text-decoration: none;
  }
  a + a {
    @include spacing(margin-left, 32);
  }
}

.newsBtn {
  width: 60px;
  height: 32px;
  background: rgba(0, 0, 0, 0.28);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;

  @media (max-width: $breakpoint-middle) {
    width: 54px;
    font-size: 13px;
  }
  @media (max-width: $breakpoint-mobile) {
    padding-left: calc(50% - 165px);
    padding-right: calc(50% - 165px);
    width: 52px !important;
    font-size: 12px;
  }
  line-height: 18px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  @include spacing(margin-right, 16);
}

.annoucementBarContainer {
  @extend .container;
  display: flex;
  width: 100vw;
  height: 48px;
  background: linear-gradient(90.04deg, #4568dc 22.57%, #b06ab3 118.44%);
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  .topGroup {
    flex: 1;
    display: flex;
    align-items: center;
  }
  .bottomGroup {
    display: flex;
    align-items: center;
    // justify-content: flex-end;
  }

  @media (max-width: $breakpoint-middle) {
  }
  @media (max-width: $breakpoint-mobile) {
    flex-direction: column;
    height: fit-content;
    // max-height: 170px;
    justify-content: center;
    align-items: center;
    row-gap: 16px;
    .topGroup {
      margin-top: 16px;
      flex: 0;
      flex-direction: column;
      row-gap: 16px;
      .text {
        text-align: center;
        font-size: 16px;
        white-space: pre-line;
      }
    }
    .bottomGroup {
      margin-bottom: 16px;
      justify-content: center;
      line-height: 16px;
    }
  }

  a {
    color: white;
  }

  .text {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    @include font-sizing(16);
    line-height: 20px;
    display: flex;
    align-items: center;
    flex: 1;
    color: #ffffff;
  }
}

.closeBtn {
  @include spacing(margin-left, 16);
  &:hover {
    cursor: pointer;
  }

  &:after {
    display: inline-block;
    content: "\00d7";
    @include font-sizing(32);
    color: rgba(255, 255, 255, 0.72);
  }
}
